import React from "react";
import { useEffect } from "react";
import axios from "axios";
import { useState } from "react";

export const ViewProducts = () => {
  const [productJSON, setproductJSON] = useState();
  const [brand, setbrand] = useState("maybelline");
  const [productType, setproductType] = useState("eyeshadow");

  useEffect(() => {
    axios
      .get(
        `http://makeup-api.herokuapp.com/api/v1/products.json?brand=${brand}&product_type=${productType}`
      )
      .then((response) => {
        setproductJSON(response.data);
      });
  }, [brand || productType]);

  return (
    <div>
      <div className="flex text-sm m-2">
        <div>results for</div>
        <div className="text-red-700 font-bold">"{brand} {productType}"</div>
      </div>
      <hr />
      <div className="flex">
        {/* Left side  */}
        <div className="w-1/6 px-1">
          <div className="text-left text-xs font-bold mt-2 mb-1">
            Delivery Day
          </div>
          {/* Brand  */}
          <div>
            <div className="text-left text-xs font-bold mt-2 mb-1">Brand</div>
            <button
              className="block text-xs"
              onClick={() => {
                setbrand("maybelline");
              }}
            >
              maybelline
            </button>
            <button
              className="block text-xs"
              onClick={() => {
                setbrand("covergirl");
              }}
            >
              covergirl
            </button>
            <button
              className="block text-xs"
              onClick={() => {
                setbrand("annabelle");
              }}
            >
              annabelle
            </button>
          </div>
          {/* Type  */}
          <div>
            <div className="text-left text-xs font-bold mt-2 mb-1">Type</div>
            <button
              className="block text-xs"
              onClick={() => {
                setproductType("lipstick");
              }}
            >
              lipstick
            </button>
            <button
              className="block text-xs"
              onClick={() => {
                setproductType("eyeshadow");
              }}
            >
              eyeshadow
            </button>
            <button
              className="block text-xs"
              onClick={() => {
                setproductType("foundation");
              }}
            >
              foundation
            </button>
          </div>
        </div>
        {/* Right side  */}
        <div className="w-5/6">
          {/* Products are below */}
          <div>
            <section class="text-gray-600 body-font">
              <div class="container px-5 pb-5 mx-auto">
                <div className="mx-1 my-2  font-semibold text-left">
                  RESULTS
                </div>
                <div class="flex flex-wrap w-full">
                  {/* // */}
                  {productJSON &&
                    productJSON.map((product) => {
                      return (
                        <div class="lg:w-1/4 md:w-1/2 p-4 w-full border my-1">
                          <a class="block relative h-48 rounded overflow-hidden">
                            <img
                              alt="ecommerce"
                              class="object-cover object-center w-full h-full block cursor-pointer"
                              src={product.image_link}
                            />
                          </a>
                          <div class="mt-4">
                            <h2 class="text-black cursor-pointer hover:text-red-600 title-font text-sm text-left font-semibold">
                              {product.name}
                            </h2>
                            <span class="flex items-center">
                              <svg
                                fill="currentColor"
                                stroke="currentColor"
                                stroke-linecap="round"
                                stroke-linejoin="round"
                                stroke-width="2"
                                class="w-3 h-3 text-amber-500"
                                viewBox="0 0 24 24"
                              >
                                <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z"></path>
                              </svg>
                              <svg
                                fill="currentColor"
                                stroke="currentColor"
                                stroke-linecap="round"
                                stroke-linejoin="round"
                                stroke-width="2"
                                class="w-3 h-3 text-amber-500"
                                viewBox="0 0 24 24"
                              >
                                <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z"></path>
                              </svg>
                              <svg
                                fill="currentColor"
                                stroke="currentColor"
                                stroke-linecap="round"
                                stroke-linejoin="round"
                                stroke-width="2"
                                class="w-3 h-3 text-amber-500"
                                viewBox="0 0 24 24"
                              >
                                <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z"></path>
                              </svg>
                              <svg
                                fill="currentColor"
                                stroke="currentColor"
                                stroke-linecap="round"
                                stroke-linejoin="round"
                                stroke-width="2"
                                class="w-3 h-3 text-amber-500"
                                viewBox="0 0 24 24"
                              >
                                <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z"></path>
                              </svg>
                              <svg
                                fill="none"
                                stroke="currentColor"
                                stroke-linecap="round"
                                stroke-linejoin="round"
                                stroke-width="2"
                                class="w-3 h-3 text-amber-500"
                                viewBox="0 0 24 24"
                              >
                                <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z"></path>
                              </svg>
                              <span class="text-gray-600 text-xs ml-1">
                                {Math.floor(product.price * 9)} Reviews
                              </span>
                            </span>
                            <div className="flex">
                              <p>₹</p>
                              <p class="text-xl text-black font-semibold">
                                {Math.ceil(product.price * 20)}
                              </p>
                            </div>
                            <p className="text-xs text-left">
                              Buy 2 items, get 5% off
                            </p>
                            <div className="flex my-1">
                              <p className="text-xs text-left">Get it by</p>
                              <p className="text-xs text-left font-bold ml-1">
                                Tomorrow
                              </p>
                            </div>
                            <p className="text-xs text-left">
                              FREE Delivery over ₹499. Fulfilled by Amazon
                            </p>
                          </div>
                        </div>
                      );
                    })}
                  {/* // */}
                </div>
              </div>
            </section>
          </div>
        </div>
      </div>
    </div>
  );
};
