import React, { useEffect, useState } from "react";
// import "../home/slide.css";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
// import { products } from "./productData";
import { Divider } from "@mui/material";
import { NavLink } from "react-router-dom";
import axios from "axios";

const responsive = {
  desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 4,
  },
  tablet: {
    breakpoint: { max: 1024, min: 464 },
    items: 2,
  },
  mobile: {
    breakpoint: { max: 464, min: 0 },
    items: 1,
  },
};

const Slide = ({ title, products, productType }) => {
  const [productJSON, setproductJSON] = useState();

  useEffect(() => {
    axios
      .get(
        `http://makeup-api.herokuapp.com/api/v1/products.json?brand=maybelline&product_type=${productType}`
      )
      .then((response) => {
        setproductJSON(response.data);
      });
  }, []);

  return (
    <div className="products_section bg-white w-[98%] justify-between items-center px-[20px] m-4">
      <div className="products_deal flex items-center p-4 mb-2">
        <h3 className="font-semibold text-lg">{title}</h3>
        <button className="view_btn mx-2 text-blue-700">
          <NavLink to="/products" className="text-sm">
            View all
          </NavLink>
        </button>
      </div>
      {/* <Divider /> */}

      {/* <Carousel
        responsive={responsive}
        infinite={true}
        draggable={false}
        swipeable={true}
        centerMode={true}
        autoPlay={true}
        autoPlaySpeed={4000}
        keyBoardControl={true}
        showDots={false}
        removeArrowOnDeviceType={["tablet", "mobile"]}
        dotListClass="custom-dot-list-style"
        itemClass="carousel-item-padding-40-px"
        containerClass="carousel-container"
      >
        {products.map((e) => {
          return (
            <NavLink to={`/getproductsone/${e.id}`}>
              <div className="products_items">
                <div className="product_img">
                  <img
                    className="w-[150px] mt-[4px] "
                    src={e.url}
                    alt="product"
                  />
                </div>
                <p className="products_name mt-[10px] ">{e.title.shortTitle}</p>
                <p
                  className="products_offer mt-[10px] "
                  style={{ color: "#  007185" }}
                >
                  {e.discount}
                </p>
                <p className="products_explore mt-[10px] ">{e.tagline}</p>
              </div>
            </NavLink>
          );
        })}
      </Carousel> */}
      {/* // */}
      {productJSON && (
        <Carousel
          responsive={responsive}
          infinite={true}
          draggable={false}
          swipeable={true}
          centerMode={true}
          autoPlay={true}
          autoPlaySpeed={4000}
          keyBoardControl={true}
          showDots={false}
          removeArrowOnDeviceType={["tablet", "mobile"]}
          dotListClass="custom-dot-list-style"
          itemClass="carousel-item-padding-40-px"
          containerClass="carousel-container"
        >
          {productJSON.map((product) => {
            return (
              <>
                <NavLink to={`/getproductsone/${product.id}`}>
                  <div className="products_items">
                    <div className="product_img">
                      <img
                        className="w-[150px] mt-[4px] "
                        src={product.image_link}
                        alt="product"
                      />
                    </div>
                    <p className="products_name mt-[10px] text-left px-1">
                      {product.name}
                    </p>
                    <p
                      className="products_offer mt-[10px] "
                      style={{ color: "#  007185" }}
                    >
                      <div className="flex">
                        <p>₹</p>
                        <p class="text-black">
                          {Math.ceil(product.price * 20)}
                        </p>
                      </div>
                    </p>
                    {/* <p className="products_explore mt-[10px] ">
                      {product.tagline}
                    </p> */}
                  </div>
                </NavLink>
              </>
            );
          })}
        </Carousel>
      )}
      {/* // */}
    </div>
  );
};

export default Slide;
